import React, { useState } from 'react'
import Typography from '@material-ui/core/Typography'
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardMedia from '@material-ui/core/CardMedia';
import CardContent from '@material-ui/core/CardContent';
import Popup from 'reactjs-popup'
import ArtOverlayCard from './ArtOverlayCard'
import cx from 'clsx';
import { makeStyles } from '@material-ui/core/styles';


const useStyles = makeStyles(({ breakpoints, spacing }) => ({
    root: {
        maxWidth: 450,
        margin: 'auto',
        borderRadius: 10,
        padding: 20,
        minHeight: 300,
        borderRadius: "10px",


    },
    media: {
        borderRadius: 1,

        margin: 'auto'
    },


}));

function ArtCard(props) {


    const styles = useStyles();

    const pic = props.pic
    const [modalVisible, setModalVisible] = useState(false);
    const triggerCard = <Card sx={{ maxWidth: 345 }}
        className={cx(styles.root)}

        onClick={() => setModalVisible(true)}>
        <CardHeader

            subheader={`@${pic.author}`}
            style={{ textAlign: "left" }}
        />
        <CardMedia
            component="img"
            height="194"
            image={pic.src}
            alt="Paella dish"
        />
        <CardContent>
            <Typography gutterBottom variant="h5" component="div" style={{ fontFamily: 'Roboto', textAlign: 'left' }}>
                {pic.title}
            </Typography>
            <Typography variant="body2" color="text.secondary"
                style={{ textAlign: "left", backgroundColor: "yellow" }}>
                Price: {pic.price}
            </Typography>
        </CardContent>
    </Card>
    const modalCard = <ArtOverlayCard pic={pic}></ArtOverlayCard>



    return (
        <Popup
            trigger={triggerCard}
            modal
            nested
        >
            {close => (
                <div className="orama-modal-art">
                    {modalCard}
                </div>
            )}
        </Popup>
    )
}

export default ArtCard
